import {
  LOGIN_TRY,
  LOGIN_SUCCESS,
  LOGIN_FAIL_USER,
  LOGOUT,
  TOGGLE_ERRORS
} from "../actions/login";

const initialState = {
  isLogged: false,
  loginFail: false,
  data: null,
  wannaSeeErrors: true
};

const loginReducer = (state = initialState, action) => {
  switch (action.type) {
    case LOGIN_TRY:
      return {
        ...state,
        isLogged: false,
        loginFail: false,
        data: null,
        wannaSeeErrors: true
      };
    case LOGIN_FAIL_USER: {
      return {
        ...state,
        loginFail: true
      };
    }
    case LOGIN_SUCCESS: {
      return {
        ...state,
        isLogged: true,
        data: action.payload
      };
    }
    case LOGOUT: {
      return {
        ...state,
        isLogged: false,
        data: null
      };
    }
    case TOGGLE_ERRORS: {
      return {
        ...state,
        wannaSeeErrors: !state.wannaSeeErrors
      };
    }
    default:
      return state;
  }
};

export default loginReducer;
