/* global $ */
import React, { Component } from "react";
import styled from "styled-components";
import { connect } from "react-redux";
import { logout, toggleErrors } from "../../actions/login";
import Error from "../Error/Error";
import Menti from "../Menti/Menti";
import Task from "../Task/Task";

const Jumbo = styled.div`
  width: 90%;
  background: #171830;
  color: #fcfcfc;
  margin: 2% auto;
`;
const Hr = styled.hr`
  background: white;
`;
const Table = styled.table`
  margin: 0 auto;
`;
const TableHolder = styled.div`
  overflow-x: auto;
  width: 95%;
  margin: 0 auto;
`;
const Div = styled.div`
  color: white;
  font-size: 1.5rem;
`;
class Dashboard extends Component {
  state = {
    hidden: false
  };
  idBase = new Set();
  createId = () => {
    const { idBase } = this;
    let id;
    do {
      id = (Math.random() * 10000).toString(16);
    } while (idBase.has(id));
    idBase.add(id);

    return id;
  };

  logoutHandler = event => {
    event.preventDefault();
    const { logout } = this.props;
    logout();
  };

  componentDidMount = () => {
    $('[data-toggle="popover"]').popover();
    if (this.props.wannaSeeErrors) {
      $(".toast").toast("show");
    }
    $(window).click(e => {
      if (
        $(e.target).hasClass("popover") ||
        $(e.target).hasClass("popover-header") ||
        $(e.target).hasClass("popover-body") ||
        $(e.target).hasClass("arrow")
      ) {
        return;
      }
      Array.from($('[data-toggle="popover"]')).forEach(button => {
        if (e.target !== button) {
          $(button).popover("hide");
        }
      });
    });
  };

  createMentiTemplate = () => {
    const { createId, props } = this;
    const { mentor } = props.data;

    return mentor.menti.map(menti => {
      return <Menti createId={createId} key={createId()} menti={menti} />;
    });
  };

  createTaskTemplate = () => {
    const { createId, props } = this;
    const { tasks } = props.data;

    return Object.keys(tasks).map(task => {
      return <Task key={createId()} task={task} />;
    });
  };

  createErrorTemplate = () => {
    const { createId, props } = this;
    const { errors } = props.data;
    return Object.keys(errors).map(error => {
      return <Error key={createId()} error={error} />;
    });
  };

  render() {
    const {
      createMentiTemplate,
      createTaskTemplate,
      createErrorTemplate,
      props,
      logoutHandler
    } = this;
    const { data, toggleErrors, wannaSeeErrors } = props;
    return (
      <div
        aria-live="polite"
        aria-atomic="true"
        style={{ position: "relative" }}
      >
        <Jumbo className="jumbotron">
          <h1 className="display-4">
            <a
              className="text-light"
              href={`https://github.com/${data.mentor.gitHubLink.toLowerCase()}`}
              target="_blank"
              rel="noopener noreferrer"
            >
              {data.name}
            </a>{" "}
            , {data.mentor.city}
          </h1>
          <button
            type="button"
            className="btn btn-dark btn-outline-light mr-3"
            onClick={logoutHandler}
          >
            Logout
          </button>
          <button
            type="button"
            className="btn btn-dark btn-outline-light"
            onClick={toggleErrors}
          >
            {wannaSeeErrors ? "Hide errors on load" : "Show errors on load"}
          </button>
          <div className="dropdown mt-2">
            <button
              className="btn btn-dark btn-outline-light dropdown-toggle"
              type="button"
              id="dropdownMenuButton"
              data-toggle="dropdown"
              aria-haspopup="true"
              aria-expanded="false"
            >
              Legend
            </button>
            <div
              className="dropdown-menu p-1-0"
              aria-labelledby="dropdownMenuButton"
            >
              <Div
                className="dropdown-item mb-1"
                style={{
                  backgroundColor: "#00592f"
                }}
              >
                Checked
              </Div>
              <Div
                className="dropdown-item mb-1"
                style={{
                  backgroundColor: "#706d17"
                }}
              >
                In progress
              </Div>
              <Div
                className="dropdown-item mb-1"
                style={{
                  backgroundColor: "#7c3535"
                }}
              >
                Must check
              </Div>
              <Div
                className="dropdown-item mb-1"
                style={{
                  backgroundColor: "#421111"
                }}
              >
                Deadline over
              </Div>
              <Div
                className="dropdown-item mb-1"
                style={{
                  backgroundColor: "#424242"
                }}
              >
                To do
              </Div>
            </div>
          </div>
          <p className="lead">
            Yor name, menties and PR buttons are links, score has popovers with
            comment if was and date
          </p>
          <p className="lead">
            Yellow errors was solved by script, red was missed
          </p>
          <Hr className="my-4" />
          <TableHolder>
            <Table className="table table-striped table-bordered table-dark">
              <thead>
                <tr className="mentor-score-head">
                  <th scope="col">#</th>
                  {createTaskTemplate()}
                </tr>
              </thead>
              <tbody className="mentor-score-body">
                {createMentiTemplate()}
              </tbody>
            </Table>
          </TableHolder>
        </Jumbo>
        <div
          style={{
            position: "absolute",
            bottom: 0,
            right: 0
          }}
        >
          {wannaSeeErrors && createErrorTemplate()}
        </div>
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    data: state.login.data,
    wannaSeeErrors: state.login.wannaSeeErrors
  };
};

const mapDispatchToProps = dispatch => {
  return {
    logout: () => dispatch(logout()),
    toggleErrors: () => dispatch(toggleErrors())
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Dashboard);
