import store from "../store/storeConfig";

const LOGIN_TRY = "LOGIN_TRY";
const LOGIN_SUCCESS = "LOGIN_SUCCESS";
const LOGIN_FAIL_USER = "LOGIN_FAIL_USER";
const LOGOUT = "LOGOUT";
const TOGGLE_ERRORS = "TOGGLE_ERRORS ";

const tryLogin = login => {
  return dispatch => {
    dispatch({
      type: LOGIN_TRY
    });
    const { data } = store.getState().data;
    const mentor = Object.keys(data).find(
      mentorData => data[mentorData].gitHubLink === login.toUpperCase()
    );
    if (mentor === undefined) {
      dispatch({
        type: LOGIN_FAIL_USER
      });
    } else {
      dispatch({
        type: LOGIN_SUCCESS,
        payload: {
          mentor: data[mentor],
          name: mentor,
          tasks: data.tasks,
          errors: data.errors
        }
      });
    }
  };
};
const logout = () => {
  return {
    type: LOGOUT
  };
};

const toggleErrors = () => {
  return {
    type: TOGGLE_ERRORS
  };
};

export {
  TOGGLE_ERRORS,
  toggleErrors,
  tryLogin,
  logout,
  LOGOUT,
  LOGIN_TRY,
  LOGIN_SUCCESS,
  LOGIN_FAIL_USER
};
