import { FETCH_REQUEST, FETCH_SUCCESS, FETCH_FAIL } from "../actions/data";

const initialState = {
  isLoading: false,
  isLoaded: false,
  isLoadError: false,
  data: null
};

const dataReducer = (state = initialState, action) => {
  switch (action.type) {
    case FETCH_REQUEST:
      return {
        ...state,
        isLoading: true,
        isLoaded: false,
        isLoadError: false,
        data: null
      };
    case FETCH_SUCCESS: {
      return {
        ...state,
        isLoading: false,
        isLoaded: true,
        data: action.payload
      };
    }
    case FETCH_FAIL: {
      return {
        ...state,
        isLoading: false,
        isLoadError: true
      };
    }
    default:
      return state;
  }
};

export default dataReducer;
