import { createStore, applyMiddleware } from "redux";
import thunk from "redux-thunk";
import rootReducer from "../reducers";

const loadStore = () => {
  let lastStore = localStorage.getItem("lastStore");

  if (lastStore) {
    return JSON.parse(lastStore);
  }
  return {};
};

const store = createStore(rootReducer, loadStore(), applyMiddleware(thunk));

store.subscribe(() => {
  const serializedStore = JSON.stringify(store.getState());
  localStorage.setItem("lastStore", serializedStore);
});

export default store;
