/* global $ */

import React, { Component } from "react";
import { connect } from "react-redux";
import styled from "styled-components";

const Holder = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
`;

class Mark extends Component {
  colorize = (task, menti) => {
    const { tasks, mentor } = this.props.data;
    const status = tasks[task].status.toLowerCase();
    if (mentor.marks[task] && mentor.marks[task][menti]) {
      return "#00592f";
    } else if (status === "in progress") {
      return "#706d17";
    } else if (status === "checking") {
      return "#7c3535";
    } else if (status === "checked") {
      return "#421111";
    } else {
      return "#424242";
    }
  };
  defaultDateView = dateString => {
    const date = new Date(dateString);
    const day = date.getDate() >= 10 ? date.getDate() : `0${date.getDate()}`;
    const mounth =
      date.getMonth() + 1 >= 10
        ? date.getMonth() + 1
        : `0${date.getMonth() + 1}`;
    const year =
      date.getFullYear() >= 10 ? date.getFullYear() : `0${date.getFullYear()}`;
    const hour =
      date.getHours() >= 10 ? date.getHours() : `0${date.getHours()}`;
    const minutes =
      date.getMinutes() >= 10 ? date.getMinutes() : `0${date.getMinutes()}`;
    return `${day}.${mounth}.${year}
    ${hour}:${minutes}`;
  };
  render() {
    const { props, colorize, defaultDateView } = this;
    const { mentor } = props.data;
    const { task, menti } = props;
    const color = colorize(task, menti);
    return (
      <td
        className={`score menti-${menti}-score ${task
          .replace(/\W/g, "")
          .replace(/,/g, "")}-score`}
        style={{ backgroundColor: color }}
      >
        {mentor.marks[task] && mentor.marks[task][menti] && (
          <Holder>
            <button
              className="btn btn-dark btn-outline-light m-2"
              type="button"
              data-toggle="popover"
              data-placement="top"
              style={{ fontSize: "1.5rem" }}
              title={defaultDateView(mentor.marks[task][menti].date)}
              data-content={mentor.marks[task][menti].comment}
              onClick={e => {
                $(e.target).popover();
              }}
            >
              {mentor.marks[task][menti].score}
            </button>
            <a
              className="btn btn-dark btn-outline-light"
              href={mentor.marks[task][menti].pullRequestLink}
              role="button"
              target="_blank"
              rel="noopener noreferrer"
            >
              PR
            </a>
          </Holder>
        )}
      </td>
    );
  }
}

const mapStateToProps = state => {
  return {
    data: state.login.data
  };
};

export default connect(mapStateToProps)(Mark);
