const FETCH_REQUEST = "FETCH_REQUEST";
const FETCH_SUCCESS = "FETCH_SUCCESS";
const FETCH_FAIL = "FETCH_FAIL";

const fetchData = () => {
  return dispatch => {
    dispatch({
      type: FETCH_REQUEST
    });

    fetch("./data/data.json")
      .then(blob => blob.json())
      .then(data => {
        console.log(data);
        dispatch({
          type: FETCH_SUCCESS,
          payload: data
        });
      })
      .catch(err => {
        console.log(err);
        dispatch({
          type: FETCH_FAIL
        });
        return;
      });
  };
};

export { FETCH_REQUEST, FETCH_SUCCESS, FETCH_FAIL, fetchData };
