/* global $ */
import React, { Component } from "react";
import styled from "styled-components";
import firebase from "firebase";
import "firebase/app";
import "firebase/auth";
import { connect } from "react-redux";
import { tryLogin } from "../../actions/login";
import { fetchData } from "../../actions/data";
import logo from "./github.png";

const Jumbo = styled.div`
  width: 40%;
  min-width: 700px;
  margin: 10% auto;
  background: #171830;
  color: #fcfcfc;
`;
const Input = styled.div`
  width: 80%;
`;
const Hr = styled.hr`
  background: white;
`;
const Img = styled.img`
  width: 4rem;
`;
class Login extends Component {
  state = {
    value: ""
  };
  logInHandler = event => {
    event.preventDefault();
    const { value } = this.state;
    const { tryLogin } = this.props;

    tryLogin(value);
  };
  handleChange = event => {
    this.setState({
      value: event.target.value
    });
  };
  handleAutocomplete = () => {
    const { data } = this.props.data;
    const gitHub = Object.keys(data).map(name => {
      return {
        label: data[name].gitHubLink,
        value: data[name].gitHubLink
      };
    });
    const save = (event, ui) => {
      if (ui.item) {
        this.setState({
          value: ui.item.value
        });
      }
    };
    $("#app-login").autocomplete({
      source: Array.from(gitHub),
      select: save
    });
  };
  logGit = e => {
    const { tryLogin } = this.props;
    e.preventDefault();
    const config = {
      apiKey: "AIzaSyBjhm65_HRSeI3M7fTYz7on3TFv7YZvDkc",
      authDomain: "dashboard-5b4a8.firebaseapp.com",
      databaseURL: "https://dashboard-5b4a8.firebaseio.com",
      projectId: "dashboard-5b4a8",
      storageBucket: "dashboard-5b4a8.appspot.com",
      messagingSenderId: "449807786079"
    };
    if (firebase.apps.length) {
      firebase.app();
    } else {
      firebase.initializeApp(config);
    }
    const provider = new firebase.auth.GithubAuthProvider();
    firebase
      .auth()
      .signInWithPopup(provider)
      .then(result => {
        this.setState({
          value: result.additionalUserInfo.username.toUpperCase()
        });
        tryLogin(result.additionalUserInfo.username);
      })
      .catch(function(error) {
        tryLogin("");
      });
  };
  render() {
    const { value } = this.state;
    const { login, data, fetchData } = this.props;
    if (!data.isLoaded) {
      if (!data.isLoading) {
        fetchData();
      }
    } else {
      this.handleAutocomplete();
    }

    return (
      <Jumbo className="jumbotron">
        <h1 className="display-4">Please, Sing in</h1>
        {data.isLoadError && (
          <p className="lead text-danger">Faled to load recoures</p>
        )}
        {login.loginFail && (
          <p className="lead text-danger">Mentor doesn't exist</p>
        )}
        <p className="lead">Use your github account nickname</p>
        <Hr className="my-4" />
        {data.isLoading ? (
          <div className="spinner-border text-light" role="status">
            <span className="sr-only">Loading...</span>
          </div>
        ) : (
          <form onSubmit={this.logInHandler}>
            <Input className="form-group input-group">
              <input
                type="text"
                className="form-control"
                id="app-login"
                placeholder="Enter login"
                value={value}
                autoComplete="off"
                onChange={this.handleChange}
              />

              <div className="input-group-append">
                <button
                  type="submit"
                  className="btn btn-light btn-outline-dark"
                >
                  Submit
                </button>
              </div>
            </Input>
            <Img
              type="button"
              src={logo}
              onClick={this.logGit}
              alt="gitHubLogo"
              className="btn btn-dark btn-outline-danger"
            />
          </form>
        )}
      </Jumbo>
    );
  }
}

const mapStateToProps = state => {
  return {
    data: state.data,
    login: state.login
  };
};

const mapDispatchToProps = dispatch => {
  return {
    tryLogin: login => dispatch(tryLogin(login)),
    fetchData: () => dispatch(fetchData())
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Login);
