import React, { Component } from "react";
import { connect } from "react-redux";
import Mark from "../Mark/Mark";

class Menti extends Component {
  createMarksTemplate = menti => {
    const { props } = this;
    const { createId } = props;
    const { tasks } = props.data;

    return Object.keys(tasks).map(task => {
      return <Mark key={createId()} task={task} menti={menti} />;
    });
  };

  render() {
    const { props, createMarksTemplate } = this;
    const { menti } = props;
    return (
      <tr className={`menti-${menti}`}>
        <th>
          <a
            className="text-light"
            href={`https://github.com/${menti.toLowerCase()}`}
            target="_blank"
            rel="noopener noreferrer"
          >
            {menti}
          </a>
        </th>
        {createMarksTemplate(menti)}
      </tr>
    );
  }
}

const mapStateToProps = state => {
  return {
    data: state.login.data
  };
};

export default connect(mapStateToProps)(Menti);
